.home_background{
  -webkit-transition: .8s ease-out;
  -o-transition: .8s ease-out;
  transition:.8s ease-out;
  width:100%;
  height:100vh;
  overflow:hidden;
  position:fixed;
  left:0px;
}
.home_background--hidden{
  left:-100%;
}
.home_background__video{
  position:absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  -ms-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  background-size: cover;
}

.home_bar{
  position:fixed;
  width:100%;
  height:100px;
  background-color: #101010;
  -webkit-transition: 0.8s ease-out;
  -o-transition: 0.8s ease-out;
  transition:0.8s ease-out;
  bottom: 0px;
}

.home_bar--hidden{
  bottom: -100px;
}

.logo{
  position: absolute;
  top: 25px;
  width: 100%;
  mix-blend-mode: difference;
  z-index: 100;
  will-change: opacity;
  opacity: 0;
  pointer-events: none;

  -webkit-transition: .8s ease-out;
  -o-transition: .8s ease-out;
  transition:.8s ease-out;
}
.logo--display{
  opacity: 1;
  pointer-events: auto;
}
.logo_inner{
  background: transparent;
  cursor: pointer;
  height:  60px;
  position: absolute;
  left: 25px;
  z-index: 11;
}
.logo_link{
  text-decoration: none;
  font-size:24px;
  letter-spacing: 4px;
  color: #fff;
  font-family:'Roboto', Helvetica;
  -webkit-transition: .4s ease-out;
  -o-transition: .4s ease-out;
  transition:.4s ease-out;
}
.logo_link:hover, .logo_link:focus{
  text-decoration: none;
}
.contact{
  position: fixed;
  bottom:25px;
  width: 100%;
  mix-blend-mode: difference;
  z-index: 100;
  will-change: opacity;
  -webkit-transition: .8s ease-out;
  -o-transition: .8s ease-out;
  transition:.8s ease-out;
  height:  3em;
}
.contact_inner{
  background: transparent;
  height:  3em;
  position: absolute;
  left: 25px;
  z-index: 11;
}
.contact_link{
  text-decoration: none;
  font-size:16px;
  line-height: 130%;
  letter-spacing: 1px;
  color: #fff;
  font-family:'Roboto', Helvetica;
  -webkit-transition: .4s ease-out;
  -o-transition: .4s ease-out;
  transition:.4s ease-out;

}


