.projects_wrapper{
  height: 100vh;
  position: absolute;
  width: 100%;
  overflow: hidden;
  pointer-events: none;
}

.projects_wrapper--display{
  pointer-events: auto;
}

.projects_thumbnail1{
  height: 25vh;
  position: relative;
  left: 100vw;
  -webkit-transition: 0.5s ease-out;
  -o-transition: 0.5s ease-out;
  transition:0.5s ease-out;
  overflow: hidden;
  position: relative;
  pointer-events: none;
}

.resizing{
  -webkit-transition: 0s ease-out;
  -o-transition: 0s ease-out;
  transition:0s ease-out;
}

.projects_thumbnail2{
  height: 25vh;
  position: relative;
  left: 100vw;
  -webkit-transition: 1.0s ease-out;
  -o-transition: 1.0s ease-out;
  transition:1.0s ease-out;
  overflow: hidden;
  pointer-events: none;
}
.projects_thumbnail3{
  height: 25vh;
  position: relative;
  left: 100vw;
  -webkit-transition: 1.5s ease-out;
  -o-transition: 1.5s ease-out;
  transition:1.5s ease-out;
  overflow: hidden;
  pointer-events: none;
}
.projects_thumbnail4{
  height: 25vh;
  position: relative;
  left: 100vw;
  -webkit-transition: 0.4s ease-out;
  -o-transition: 2.0s ease-out;
  -webkit-transition: 2.0s ease-out;
  transition: 2.0s ease-out;
  overflow: hidden;
  pointer-events: none;
}

.projects_thumbnail--display{
  pointer-events: auto;
  left: 0px;
}

.projects_thumbnail__outer{
  height: 100%;
  width: 100%;
}

.project_thumbnail__overlay{
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.8);
  position: absolute;
  top:0;
  display: none;
  pointer-events: none;
}
.projects_thumbnail__inner{
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  -webkit-transform: scale(1, 1);
      -ms-transform: scale(1, 1);
          transform: scale(1, 1);

  background-repeat: no-repeat!important;
  background-position: right top!important;
  background-size: cover !important;

  -webkit-transition: 0.2s ease-out;
  -o-transition: 0.2s ease-out;
  transition: 0.2s ease-out;
}
.projects_thumbnail__inner:hover{
  -webkit-transform: scale(1.05, 1.05);
      -ms-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
}

.projects_left{
  position:absolute;
  color: #fff;
  left: 25px;
  top: 45vh;
  font-size: 5em;
  cursor: pointer;
  -webkit-transition: 0.4s ease-out;
  -o-transition: 0.4s ease-out;
  transition:0.4s ease-out;
  opacity:0;
  pointer-events: none;

  -webkit-user-select: none;  /* Chrome all / Safari all */
  -moz-user-select: none;     /* Firefox all */
  -ms-user-select: none;      /* IE 10+ */
}

.projects_right{
  position:absolute;
  color: #fff;
  right: 25px;
  top: 45vh;
  font-size: 5em;
  cursor: pointer;
  -webkit-transition: 0.4s ease-out;
  -o-transition: 0.4s ease-out;
  transition:0.4s ease-out;
  opacity:0;
  pointer-events: none;

  -webkit-user-select: none;  /* Chrome all / Safari all */
  -moz-user-select: none;     /* Firefox all */
  -ms-user-select: none;      /* IE 10+ */
}

.projects_left--display{
  opacity:1;
  pointer-events: auto;
}
.projects_right--display{
  opacity:1;
  pointer-events: auto;
}

.project_thumbnail__text__left,
.project_thumbnail__text__left2{
  margin-top:20%;
  color: #ddd;
  font-family: 'Open Sans', Helvetica;
  font-size: .8em;
  -webkit-transition: 1.2s ease-out;
  -o-transition: 1.2s ease-out;
  transition:1.2s ease-out;
  margin-left: 100vw;
  pointer-events: none;

  opacity:0;
}
.project_thumbnail__text__right{
  margin-top:20%;
  color: #ddd;
  font-family: 'Open Sans', Helvetica;
  font-size: .8em;
  text-align: right;
  -webkit-transition: 1.2s ease-out;
  -o-transition: 1.2s ease-out;
  transition:1.2s ease-out;
  margin-left: 100vw;
  pointer-events: none;
  opacity: 0;
}
.project_thumbnail__text__left--display,
.project_thumbnail__text__left2--display{
  margin-left: 0px;
  opacity: 1;
}
.project_thumbnail__text__right--display{
  margin-left: 0px;
  opacity: 1;
}

@media screen and (max-width: 992px) {
  .project_thumbnail__overlay{
    display:block;
  }

  .project_thumbnail__text__right{
    position: fixed;
    z-index: 2;
    text-align:left;
    margin-top: 1em;
    padding-left: 1em;
    font-size: 1.2em;
  }
  .project_thumbnail__text__left{
    position: fixed;
    z-index: 2;
    text-align: left;
    margin-top: 1em;
    padding-left: 1em;
    top: 0px;
    font-size: 1.2em;
  }
  .project_thumbnail__text__left2{
    position: fixed;
    z-index: 2;
    text-align: left;
    margin-top: 1em;
    padding-left: 1em;
    top: 25vh;
    font-size: 1.2em;
  }
}

/*--- Individual Projects --- */

.project_smallfont{
  font-size: 10px;
  letter-spacing: 4px;
  font-weight: 600;
}
.project_fillimage{
  width: 100%;
}

.project_wrapper{
  width: 100%;
  overflow-x: hidden;
}

.project_banner__container{
  height:100vh;
  width: 100%;

}
.project_banner{
  height: 100vh;
  width: 100%;
  background-color: #0c0c0d;
  overflow: hidden;
  top: 0px;


}
.project_banner_image{
  -webkit-transition: .4s ease-out;
  -o-transition: .4s ease-out;
  transition: .4s ease-out;
  height: calc(100vh - 270px);
  width: 100%;
  background-color: #141415;
  position: absolute;
  top: 0px;


}

.project_banner_text{
  height: 270px;
  width: 100%;
  background-color: #0c0c0d;
  position: absolute;
  bottom: 0px;
  color: #fff;
  padding-top: 100px;
  font-size: 10px;
  letter-spacing: 4px;
  line-height:200%;
  -webkit-transition: .4s ease-out;
  -o-transition: .4s ease-out;
  transition:.4s ease-out;

  -webkit-box-shadow: 0px 0px 95px 10px rgba(0,0,0,0.75);
  box-shadow: 0px 0px 95px 10px rgba(0,0,0,0.75);
}

.project_banner_text__small{
  color: #666;
}

.project_about{
  width:100%;
  background-color:#fff;
  font-size:16px;
  line-height:130%;
  letter-spacing:1px;
  font-family:'Open Sans', Helvetica;
  word-wrap: break-word;

}
.project_about__divider{
  height: 100px;
  width: 100%;
}

.project_back{
  position:fixed;
  top: 40px;
  left:0;
  right:0;
  margin-left:auto;
  margin-right:auto;
  mix-blend-mode: difference;
  z-index: 10;
  width:200px;
  will-change: opacity;
}
.project_back__inner{
  background:transparent;
  cursor:pointer;
  /*
  height: 60px;
  */
  position:absolute;
  left:100px;
  z-index:11;
}
.project_back__link{
  text-decoration:none;
  font-size:10px;
  letter-spacing: 2px;
  color: #fff;
  font-family:'Roboto', Helvetica;
}

.project_body{
  background-color: #0c0c0d;
  width: 100%;
  color: #ccc;
  overflow-x: hidden;
  padding-top: 100px;
  /*
  -webkit-box-shadow: 0px 0px 95px 10px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 0px 95px 10px rgba(0,0,0,0.75);
  box-shadow: 0px 0px 95px 10px rgba(0,0,0,0.75);
  */
}

.project_body_text{
  margin-bottom: 50px;
  font-size: 16px;
  line-height: 130%;
  letter-spacing: 1px;
  font-family: 'Open Sans', Helvetica;
}

.project_mobile_images{
  bottom: 0px;
  background-color: #0c0c0d;
  padding-top: 100px;
  padding-bottom: 100px;
}

.desktop_divider{
  height: 150px;
  width: 100%;
}
.mobile_divider{
  display: none;
}

@media screen and (max-width: 992px) {
  .desktop_divider{
    display: none;
  }
  .mobile_divider{
    height: 100px;
    width: 100%;
    display: block;
  }
}

/* --- Update --- */

.update{
  width:100%;
  background-color: #0c0c0d;
  color: #fff;
  -webkit-transition: .4s ease-in-out;
  -o-transition: .4s ease-in-out;
  transition:.4s ease-in-out;

  font-size:16px;
  line-height:130%;
  letter-spacing:1px;
  font-family:Open Sans, Helvetica;
}
.update:hover{
  background-color: #222;
}
.update_divider{
  height: 25px;
  width: 100%;
}

/* --- Resume --- */

.resume_downloader{
  padding-top:50px;
  height: 100vh;
}

.resume_image{
  padding-top:50px;
  -webkit-transition: .4s ease-in-out;
  -o-transition: .4s ease-in-out;
  transition:.4s ease-in-out;
}

@media screen and (max-width: 992px) {
  .resume_downloader{
    padding-top:50px;
    height: auto;
  }
}


